import { TPost } from '../types'
import clsx from 'clsx'
import { FC, useEffect, useState } from 'react'
import Card18 from './PostCards/Card18'
import Card19 from './PostCards/Card19'
import { Ad, CarouselDemo } from './homeSliderMobile'

interface Props {
  posts: TPost[]
  className?: string
  vr: 'ar' | 'fr'
  loading?: boolean
  isHome?: boolean
}

const SectionMagazine10: FC<Props> = ({
  posts,
  className,
  vr,
  loading = false,
  isHome = false,
}) => {
  const [ads, setAds] = useState<Ad[]>([])

  useEffect(() => {
    if (!isHome) return
    fetch('https://apiw.fibladi.com/api/top-ads')
      .then((response) => response.json())
      .then((data) => {
        setAds(data.data)
      })
      .catch(() => {})
  }, [])

  if (loading) {
    return (
      <div className="grid grid-cols-1 gap-4 lg:grid-cols-6">
        {/* Right column - 1 tall card */}
        <div className="h-auto rounded-lg bg-gray-300 lg:col-span-3"></div>
        {/* Left column - 3 stacked cards */}
        <div className="grid grid-cols-2 gap-4 lg:col-span-3">
          <div className="h-70  sm:h-60 col-span-2 sm:col-span-1 rounded-lg bg-gray-300"></div>
          <div className="h-70  sm:h-60 col-span-2 sm:col-span-1 rounded-lg bg-gray-300"></div>
          <div className="col-span-2 h-70  sm:h-92 rounded-lg bg-gray-300"></div>
        </div>
      </div>
    )
  }

  if (posts.length < 1) return null
  return (
    <div className={clsx('section-magazine-10 relative', className)}>
      <div className="grid grid-cols-1 gap-5 xl:grid-cols-2">
        {posts[0] && (
          <Card19
            className="shadow-lg shadow-black/30 transition-shadow"
            post={posts[0]}
            vr={vr}
            // titleClass="text-xl sm:text-2xl xl:text-2xl"
          />
        )}
        {isHome && ads.length > 0 && (
          <div className="sm:hidden -mx-1.5 -my-3 p-0" dir="ltr">
            <CarouselDemo posts={ads} isHome />
          </div>
        )}

        <div className="grid grid-cols-1 gap-5 sm:grid-cols-2 sm:grid-rows-5">
          {posts
            .filter((_, i) => i < 3 && i >= 1)
            .map((item, index) => (
              <Card18
                className="col-span-1 sm:row-span-2 shadow-lg shadow-black/30 transition-shadow"
                key={index}
                post={item}
                vr={vr}
              />
            ))}

          {posts[3] && (
            <Card19
              ratio="aspect-4/3 sm:aspect-16/1"
              className="sm:col-span-2 sm:row-span-4 shadow-lg shadow-black/30 transition-shadow"
              // titleClass="text-xl sm:text-2xl xl:text-2xl"
              vr={vr}
              post={posts[3]}
            />
          )}
        </div>
      </div>
    </div>
  )
}

export default SectionMagazine10
