import PostFeaturedMedia from './PostFeaturedMedia'
import { TPost } from '@/types'
import clsx from 'clsx'
import Image from '../image'
import { Link } from '@tanstack/react-router'
import { FC } from 'react'
import CategoryBadgeList from './CategoryBadgeList'
import PostCardSaveBtn from './PostCardSaveBtn'
// import PostTypeFeaturedIcon from './PostTypeFeaturedIcon'

interface Props {
  className?: string
  ratio?: string
  titleClass?: string
  post: TPost
  vr: 'ar' | 'fr'
}

const Card19: FC<Props> = ({
  className,
  titleClass = 'text-xl sm:text-2xl xl:text-3xl',
  ratio = 'aspect-4/3 sm:aspect-1/1',
  post,
  vr,
}) => {
  const { title, handle, featuredImage, categories, postType, id } = post

  const path = vr === 'fr' ? '/news/fr/$postId' : '/news/$postId'
  const link = decodeURIComponent(handle)

  // console.log(post.handle)
  return (
    <div
      className={clsx(
        'group post-card-19 relative flex flex-col overflow-hidden rounded-xl',
        className,
      )}
    >
      <div className={clsx('relative size-full', ratio)}>
        {postType === 'audio' ? (
          <PostFeaturedMedia post={post} vr={vr} setIsHover={() => {}} />
        ) : (
          <>
            <Image
              key={post.id}
              sizes="(max-width: 1024px) 100vw, 50vw"
              className="rounded-xl object-cover brightness-100 transition-[filter] duration-300 group-hover:brightness-75"
              src={featuredImage}
              alt={title}
              // priority
              fill
            />

            <Link
              aria-label={post.title}
              to={path}
              params={{ postId: link }}
              search={{ id: id }}
              className="absolute inset-0"
            ></Link>
          </>
        )}
      </div>

      <Link
        aria-label={post.title}
        to={path}
        params={{ postId: link }}
        search={{ id: id }}
        className="absolute inset-x-0 bottom-0 block h-1/2 bg-linear-to-t from-black opacity-80"
      />

      <div className="absolute inset-x-0 top-0 flex flex-wrap gap-x-2 gap-y-1 p-5 opacity-0 transition-opacity duration-300 group-hover:opacity-100 sm:px-7">
        <PostCardSaveBtn className="ms-auto" handle={post.short_link} vr={vr} />
      </div>

      <div className="absolute inset-x-0 bottom-0 flex grow flex-col p-5 sm:p-8">
        <Link
          aria-label={post.title}
          to={path}
          params={{ postId: link }}
          search={{ id: id }}
          className="absolute inset-0"
        />
        <CategoryBadgeList
          key={`${id}-category`}
          categories={categories}
          vr={vr}
        />
        <h2
          className={clsx(
            'mt-3 leading-snug font-semibold text-white line-clamp-2',
            titleClass,
          )}
          dangerouslySetInnerHTML={{ __html: title }}
        />
      </div>
    </div>
  )
}

export default Card19
