import Heading from '@/shared/Heading'
import SectionSliderPosts from './PostCards/SectionSliderPosts'
import { TPost } from '@/types'
import { ChevronRight, ChevronLeft } from 'lucide-react'
import { Link } from '@tanstack/react-router'

const Trening = ({
  posts,
  vr,
  loading = false,
}: {
  posts: TPost[]
  vr: 'ar' | 'fr'
  loading?: boolean
}) => {
  const path =
    vr === 'fr' ? '/news/fr/trending-6hours' : '/news/trending-6hours'

  if (loading) {
    return (
      <div className="space-y-4 mt-5">
        <div className="bg-gray-300 h-7 w-1/2 rounded"></div>
        <div className="grid grid-cols-1 sm:grid-cols-4 gap-8">
          {[1, 2, 3, 4].map((_, index) => (
            <div>
              <div
                key={index}
                className="animate-pulse rounded-3xl overflow-hidden "
              >
                <div className="bg-gray-300 h-32 sm:h-44 w-full"></div>
              </div>

              <div className="py-4 space-y-2">
                <div className="bg-gray-300 h-5 w-full rounded"></div>
                <div className="bg-gray-300 h-5 w-5/6 rounded"></div>
              </div>
            </div>
          ))}
        </div>
      </div>
    )
  }
  if (posts.length < 3) return null

  return (
    <div className="relative mb-4 mt-5">
      <Heading level={1}>
        <Link to={path} className="flex items-center space-x-4 ">
          {vr === 'ar'
            ? 'ترند، الأكثر رواجًا'
            : 'Trending, les plus populaires '}
          {vr === 'fr' ? (
            <ChevronRight size={19} strokeWidth={3} className="mt-1" />
          ) : (
            <ChevronLeft size={19} strokeWidth={3} className="mt-1" />
          )}
        </Link>
      </Heading>
      <SectionSliderPosts
        posts={posts}
        perView={4}
        showViewAll
        viewAllLinkHref={vr === 'fr' ? '/news/fr/trending' : '/news/trending'}
        // className="overflow-hidden"
        vr={vr}
      />
    </div>
  )
}

export default Trening
